import { useQuery } from '@abyss/web/hooks/useQuery';
import { config } from '@abyss/web/tools/config';
import GetRepoUserAccess from './GetRepoUserAccess.graphql';

export const useGetRepoUserAccess = (options) => {
  return useQuery(GetRepoUserAccess, {
    ...options,
    url: config('GRAPHQL_API_PATH'),
    dataKey: 'getRepoUserAccess',
    initialState: {},
  });
};
