import { useQuery } from '@abyss/web/hooks/useQuery';
import { config } from '@abyss/web/tools/config';
import CreateSandboxRepo from './CreateSandboxRepo.graphql';

export const useCreateSandboxRepo = (options) => {
  return useQuery(CreateSandboxRepo, {
    ...options,
    url: config('GRAPHQL_API_PATH'),
    dataKey: 'createSandboxRepository',
    initialState: {
      data: {},
      name: '',
      fullName: '',
      organization: '',
      url: '',
    },
  });
};
