import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { styled } from '@abyss/web/tools/styled';
import { IconMaterial } from '@abyss/web/ui/IconMaterial';

export const SearchBox = styled('input', {
  width: 'max(25vw, 220px)',
  transition: 'width 1s ease',
  fontSize: '15px',
  background: '$gray2',
  border: '2px solid $gray3',
  borderRadius: '5px',
  padding: '3px 5px',
  '&:hover': {
    border: '2px solid $gray4',
  },
  '&:focus-visible': {
    boxShadow: 'none',
  },
  '&::placeholder': {
    fontStyle: 'italic',
  },
});
const Icon = styled(IconMaterial, {
  padding: '1px',
  fill: '$gray8 !important',
  transform: 'translate(-120%, 5px)',
  marginRight: '-20px',
  '&:hover': {
    cursor: 'pointer',
  },
});

export const SearchBar = ({ placeholder, value, setValue }) => {
  const [icon, setIcon] = useState('search');
  useEffect(() => {
    if (value !== '') {
      setIcon('clear');
    } else {
      setIcon('search');
    }
  }, [value]);

  const onEdit = (input) => {
    setValue(input);
  };

  const onClear = () => {
    onEdit('');
  };

  const onSubmit = () => {
    setValue('');
  };

  const handleKeyDown = (event) => {
    if (event.key === 'Enter') {
      onSubmit();
    }
  };

  return (
    <React.Fragment>
      <SearchBox
        className="input"
        placeholder={placeholder}
        value={value}
        onChange={(e) => {
          return onEdit(e.target.value);
        }}
        autoFocus={value !== ''}
        onKeyDown={handleKeyDown}
      />
      <Icon icon={icon} onClick={onClear} size={20} />
    </React.Fragment>
  );
};
