import { Button } from '@src/common/Button';
import { Modal } from '@abyss/web/ui/Modal';
import { Layout } from '@abyss/web/ui/Layout';

const StyledModal = styled(Modal, {
  '&.abyss-modal-content-container': {
    padding: '10px',
  },
});

export const CreateModal = ({ isOpen, onClose, onSubmit }) => {
  return (
    <StyledModal title="Repository Created" isOpen={isOpen} onClose={onClose}>
      <Modal.Section alignLayout="center">
        Your repository has been created. Click here to see it.
      </Modal.Section>
      <Modal.Section>
        <Layout.Group alignLayout="center">
          <Button onClick={onSubmit}>Let's Go</Button>
        </Layout.Group>
      </Modal.Section>
    </StyledModal>
  );
};
