import React from 'react';
import { styled } from '@abyss/web/tools/styled';
import { Link } from '@abyss/web/ui/Link';
import { Layout } from '@abyss/web/ui/Layout';
import { Checkbox } from '@abyss/web/ui/Checkbox';

const RepoBox = styled('div', {
  borderColor: '$gray4',
  borderStyle: 'solid',
  borderTopWidth: 0.75,
  borderLeftWidth: 1,
  borderRightWidth: 1,
  borderBottomWidth: 0.75,
  padding: '$md',

  '&:first-child': {
    borderTopLeftRadius: 5,
    borderTopRightRadius: 5,
  },

  '&:last-child': {
    borderBottomLeftRadius: 5,
    borderBottomRightRadius: 5,
    borderBottomWidth: 1,
  },
});

const Description = styled('div', {
  color: '$gray6',
  fontSize: '$sm',
});

const Destination = styled('div', {
  color: '$gray6',
  fontStyle: 'italic',
});

export const RepoListItem = ({
  repo,
  org,
  prefix,
  l1,
  trackCheckbox,
  hidden,
}) => {
  return (
    <RepoBox style={hidden ? { display: 'none' } : {}}>
      <Layout.Group>
        <Checkbox
          // using isChecked as the way to keep track of whether RepoListItem is checked instead of modal, which uses indexs
          // due to changing indexs when filtering using search, making it hard to keep track of item that is checked
          // model={`repos.${index}.checked`}
          isChecked={repo.checked}
          onChange={(e) => trackCheckbox(repo.repo, e.target.checked)}
        />
        <div>
          <Link href={repo.url} hideIcon css={{ lineHeight: '20px' }}>
            {repo.repo}
          </Link>
          <Description>{repo.description}</Description>
          <Layout.Space space={4} />
          <Destination>
            migrate to: <If condition={org}>{org}/</If>
            <If condition={l1}>{l1}-</If>
            {prefix}-{repo.repo}
          </Destination>
        </div>
      </Layout.Group>
    </RepoBox>
  );
};
