import { useQuery } from '@abyss/web/hooks/useQuery';
import { config } from '@abyss/web/tools/config';
import CleanRepoSecrets from './CleanRepoSecrets.graphql';

export const useCleanRepoSecrets = (options) => {
  return useQuery(CleanRepoSecrets, {
    ...options,
    url: config('GRAPHQL_API_PATH'),
    dataKey: 'cleanRepoSecrets',
    initialState: {
      data: {},
    },
  });
};
