import React from 'react';
import { styled } from '@abyss/web/tools/styled';
import { Button } from '@src/common/Button';
import { Modal } from '@abyss/web/ui/Modal';
import { Layout } from '@abyss/web/ui/Layout';
import { IconMaterial } from '@abyss/web/ui/IconMaterial';
import { Markdown } from '@abyss/web/ui/Markdown';

const StyledModal = styled(Modal, {
  '&.abyss-modal-content-container': {
    padding: '10px',
  },
});

const IndentedMarkdown = styled(Markdown, {
  marginLeft: '$xl',
});

export const MigrationModal = ({ isOpen, onClose, onSubmit, data }) => {
  const exposedRepos = Object.keys(data).map((k) => data[k]);
  return (
    <StyledModal title="Migration Started" isOpen={isOpen} onClose={onClose}>
      <Modal.Section alignLayout="center">
        Your GitHub cloud migration has started for the selected repositories.
        Click the button below to follow their progress.
      </Modal.Section>
      {exposedRepos.length > 0 && (
        <div>
          <Modal.Section alignLayout="center">
            <IconMaterial
              icon="warning"
              color="$error1"
              size="24px"
              css={{
                'abyss-icon-material': {
                  marginRight: '15px',
                },
              }}
            />
            The following repositories will not be migrated due to exposed
            secrets:
            <Layout.Space space={16} />
            {exposedRepos.map((repo) => (
              <IndentedMarkdown>{repo}</IndentedMarkdown>
            ))}
          </Modal.Section>
        </div>
      )}
      <Modal.Section>
        <Layout.Group alignLayout="center">
          <Button onClick={onSubmit}>Let's Go</Button>
        </Layout.Group>
      </Modal.Section>
    </StyledModal>
  );
};
