import React, { useState } from 'react';
import { useCleanRepoSecrets } from '@src/hooks/github/useCleanRepoSecrets';
import { Layout } from '@abyss/web/ui/Layout';
import { Button } from '@src/common/Button';
import { TextInput } from '@abyss/web/ui/TextInput';
import { TextInputArea } from '@abyss/web/ui/TextInputArea';
import { useForm } from '@abyss/web/hooks/useForm';
import { useRouter } from '@abyss/web/hooks/useRouter';
import { FormProvider } from '@abyss/web/ui/FormProvider';
import { LoadingOverlay } from '@abyss/web/ui/LoadingOverlay';
import { Alert } from '@abyss/web/ui/Alert';
import { useGetRepoUserAccess } from '@src/hooks/github/useGetRepoUserAccess';
import { CleanSecretsModal } from '../Modals';

const GIT_REGEX = /^https?:\/\/(github(?:.optum)?|gitlab).com\/[\w-]+\/[\w-]+$/;

export const CleanRepoSecrets = () => {
  const form = useForm();
  const router = useRouter();
  const [isSubmitted, setIsSubmitted] = useState(false);
  const [hasAdmin, setHasAdmin] = useState(true);

  const [cleanRepoSecretsResult, cleanRepoSecrets] = useCleanRepoSecrets({
    onCompleted: (response) => {
      setIsSubmitted(true);
    },
  });

  const [getRepoUserAccessResult, getRepoUserAccess] = useGetRepoUserAccess({
    onCompleted: (response) => {
      setHasAdmin(response.data.isAdmin);
    },
  });

  const handleSubmit = () => {
    const formUrlValue = form.getValues(['repoUrl'])[0];
    const [targetRepo, targetOrg] = formUrlValue.split('/').reverse();
    const targetBranch = form.getValues(['branch'])[0];
    const secrets = form.getValues(['secrets'])[0];
    const variables = {
      targetOrg,
      targetRepo,
      targetBranch,
      secrets,
    };
    cleanRepoSecrets({ variables });
  };

  const handleClick = () => {
    const formUrlValue = form.getValues(['repoUrl'])[0];
    window.open(`${formUrlValue}`, '_blank', 'noreferrer');
    router.navigate('/');
  };

  const handleClose = () => {
    setIsSubmitted(false);
    router.navigate('/repository/clean');
  };

  const handleBlur = () => {
    const formUrlValue = form.getValues(['repoUrl'])[0];
    const [repo, org] = formUrlValue.split('/').reverse();

    const variables = { repo, org };

    getRepoUserAccess({ variables });
  };

  return (
    <LoadingOverlay
      isLoading={cleanRepoSecretsResult.isLoading}
      loadingTitle="Loading..."
      loadingMessage="Cleaning Repository Secrets..."
    >
      <Alert title="Action Required" variant="warning">
        In order to use this tool you MUST remove all secrets from the HEAD
        commit of your target branch.
      </Alert>
      <Layout.Space space={10} />
      <Alert title="Caution" variant="error">
        Please make sure your team has pushed any local changes, for the target
        branch, to the remote GitHub branch. You will lose ALL unsaved changes
        when running the tool below
      </Alert>
      <FormProvider state={form} onSubmit={handleSubmit}>
        <Layout.Space space={18} />
        <TextInput
          label="Repository URL"
          model="repoUrl"
          onBlur={handleBlur}
          validators={{
            required: true,
            pattern: {
              value: GIT_REGEX,
              message: 'Invalid Repository URL',
            },
          }}
          placeholder="https://github.optum.com/org/repo"
        />
        <TextInput
          label="Branch Name"
          model="branch"
          subText="Defaults to default branch"
        />
        <TextInputArea
          label="Secrets to Clean"
          rows={5}
          model="secrets"
          validators={{ required: true }}
          subText="Seperate each secret with a new line"
          placeholder="Secrets..."
        />
        <Layout.Space space={24} />
        <Alert
          title="Error"
          inlineText="You are not an admin for this repository. You do not have permission for this action."
          isVisible={!hasAdmin}
        />
        <Layout.Space space={12} />
        <Button type="submit" isDisabled={!hasAdmin}>
          Clean Repository
        </Button>
      </FormProvider>
      <CleanSecretsModal
        isOpen={isSubmitted}
        onClose={handleClose}
        onSubmit={handleClick}
        data={cleanRepoSecretsResult.data}
      />
    </LoadingOverlay>
  );
};
