import React, { useEffect, useState, useContext } from 'react';
import { Button } from '@src/common/Button';
import { TextInput } from '@abyss/web/ui/TextInput';
import { useCreateSandboxRepo } from '@src/hooks/github/useCreateSandboxRepo';
import { useGetSandboxes } from '@src/hooks/prm/useGetSandboxes';
import { useForm } from '@abyss/web/hooks/useForm';
import { useRouter } from '@abyss/web/hooks/useRouter';
import { FormProvider } from '@abyss/web/ui/FormProvider';
import { LoadingOverlay } from '@abyss/web/ui/LoadingOverlay';
import { Layout } from '@abyss/web/ui/Layout';
import { MaelstromContext } from '@src/abyss/utility/ui/MaelstromProvider/MaelstromContext';
import { CreateModal } from '../Modals';

const REPO_REGEX = /[^a-zA-Z0-9]/g;

export const CreateSandboxRepo = () => {
  const form = useForm({
    defaultValues: {
      org: 'uhg-test',
    },
  });
  const user = useContext(MaelstromContext);

  const [url, setUrl] = useState(null);

  const router = useRouter();

  const [createRepoResult, createRepo] = useCreateSandboxRepo({
    onCompleted: (response) => {
      setUrl(response.data.url);
    },
  });
  const [sandboxesResult, getSandboxes] = useGetSandboxes({
    onCompleted: (response) => {
      form.setValue('aide', response.data[0].askId);
      form.setValue('sandbox', response.data[0].name);
      form.setValue('sandboxName', response.data[0].uuid);
    },
  });

  useEffect(() => {
    getSandboxes({
      variables: {
        msid: user.username,
      },
    });
  }, []);

  const handleBlurRepoName = (e) => {
    const name = e.target.value;
    const newRepoName = name.toLowerCase().replaceAll(REPO_REGEX, '-');
    form.setValue('repositoryName', newRepoName);
  };

  const handleSubmit = (formData) => {
    const variables = {
      repo: formData.repositoryName,
      org: formData.org,
      aideId: formData.aide,
      sandboxName: formData.sandboxName,
      sandboxAlias: formData.sandbox,
    };

    createRepo({ variables });
  };

  const handleClick = () => {
    window.open(url, '_blank', 'noreferrer');
    router.navigate('/');
  };

  const handleClose = () => {
    setUrl(null);
  };

  return (
    <LoadingOverlay
      isLoading={createRepoResult.isLoading}
      loadingTitle="Loading..."
      loadingMessage="Creating Repository..."
    >
      <FormProvider state={form} onSubmit={handleSubmit}>
        <TextInput
          label="Repository Name"
          validators={{ required: true }}
          model="repositoryName"
          onBlur={handleBlurRepoName}
        />
        <TextInput
          label="Organization"
          model="org"
          validators={{ required: true }}
          isDisabled
        />
        <TextInput
          label="Sandbox"
          model="sandbox"
          validators={{ required: true }}
          isDisabled
        />
        <Layout.Space space={24} />
        <Button type="submit">Create Repository</Button>
      </FormProvider>
      <CreateModal isOpen={url} onClose={handleClose} onSubmit={handleClick} />
    </LoadingOverlay>
  );
};
