import React, { useState, useEffect } from 'react';
import { FormProvider } from '@abyss/web/ui/FormProvider';
import { Layout } from '@abyss/web/ui/Layout';
import { SelectInput } from '@abyss/web/ui/SelectInput';
import { TextInput } from '@abyss/web/ui/TextInput';
import { Button } from '@src/common/Button';
import { Checkbox } from '@abyss/web/ui/Checkbox';
import { useGetOrganizations } from '@src/hooks/github/useGetOrganizations';
import { RepoListItem } from './RepoListItem';
import { WarningModal } from '../../Modals';
import { Pagination } from '@abyss/web/ui/Pagination';
import { usePagination } from '@abyss/web/hooks/usePagination';
import { Tooltip } from '@abyss/web/ui/Tooltip';
import { IconMaterial } from '@abyss/web/ui/IconMaterial';
import { SearchBar } from '@src/common/SearchBar/SearchBar';

const PREFIX_REGEX = /[^a-zA-Z0-9]/g;

const l1Caps = require('../../../../common/ProviderData/l1Capabilities.json');

export const RepoListForm = ({ form, fields, onSubmit, collect }) => {
  const [isAckOpen, setIsAckOpen] = useState(false);
  const [hasSeen, setHasSeen] = useState(false);
  const [searchValue, setSearchValue] = useState('');
  const [filteredRepos, setFilteredRepos] = useState(fields);
  // need overall state to keep track of all reposListItems (reposState) regardless of filtered or not
  const [reposState, setReposState] = useState(fields);
  const [provider, setProvider] = useState(false);
  const [org, prefix, l1Model] = form.getValues(['org', 'prefix', 'l1Model']);
  const [organizationOptions, setOrganizationOptions] = useState([]);
  const [orgsResult, getOrgs] = useGetOrganizations({
    onCompleted: (response) => {
      const options = response.data.map((dataItem) => {
        return {
          value: dataItem.name,
          label: dataItem.name,
        };
      });

      setOrganizationOptions(options);
    },
  });

  const handleChangePrefix = (e) => {
    const prefix = e.target.value;
    const newPrefix = prefix.toLowerCase().replaceAll(PREFIX_REGEX, '-');
    form.setValue('prefix', newPrefix);
  };

  const checkOrg = (e) => {
    if (e === 'uhc-tech-provider') {
      setProvider(true);
    } else {
      setProvider(false);
    }
  };

  useEffect(() => {
    getOrgs();
  }, []);

  useEffect(() => {
    setFilteredRepos(fields);
    setReposState(fields);
  }, [fields]);

  useEffect(() => {
    filterSearch();
  }, [searchValue]);

  const handleCheckBox = (e) => {
    if (e.target.checked === true && hasSeen === false) {
      setIsAckOpen(true);
    }
  };

  const handleCancel = () => {
    form.setValue('archive', false);
    setIsAckOpen(false);
  };

  const handleAcknowledge = () => {
    setIsAckOpen(false);
    setHasSeen(true);
  };

  const reposPerPage = 8;
  const paginationProps = usePagination({
    pages: Math.ceil(filteredRepos.length / reposPerPage),
    start: 1,
  });

  // reseting pagination when filtering/searching
  useEffect(() => {
    if (
      filteredRepos.length <
      paginationProps.state.currentPage * reposPerPage - (reposPerPage - 1)
    ) {
      paginationProps.firstPage();
    } else {
      paginationProps.gotoPage(paginationProps.state.currentPage - 1);
    }
  }, [filteredRepos]);

  // filter using the reposState which has the overall state of repos and if checked or not
  const filterSearch = () => {
    let filteredData = [...reposState];
    if (searchValue) {
      const searchValueLowerCase = searchValue.toLowerCase();
      filteredData = filteredData.filter((item) => {
        return item.repo.toLowerCase().includes(searchValueLowerCase);
      });
    }
    setFilteredRepos(filteredData);
  };

  // once a repoListItem is checked, update both the reposState and filteredRepos to include checked repo
  // doing this will keep track of checkbox state of each item
  const trackCheckbox = (repoName, isChecked) => {
    const updateRepoState = reposState.map((repo) =>
      repo.repo === repoName ? { ...repo, checked: isChecked } : repo
    );

    // update for filteredRepos since filteredRepos is what pagination and filter results are based off of
    const updateFilteredRepos = filteredRepos.map((repo) =>
      repo.repo === repoName ? { ...repo, checked: isChecked } : repo
    );
    setReposState(updateRepoState);
    setFilteredRepos(updateFilteredRepos);
    // send this back to MigrateOrg to set reposToMigrate state there
    collect(updateRepoState);
  };

  return (
    <FormProvider state={form} onSubmit={onSubmit}>
      <Layout.Space space={16} />
      <SelectInput
        label="Organization"
        model="org"
        placeholder="Select a target Organization"
        validators={{ required: true }}
        isSearchable
        options={organizationOptions}
        onChange={checkOrg}
        isLoading={orgsResult.isLoading}
      />
      {provider ? (
        <SelectInput
          label="L1 - Capability Model"
          model="l1Model"
          placeholder="Select an L1 - Capability Model"
          validators={{ required: true }}
          isSearchable
          options={l1Caps}
        />
      ) : null}
      <TextInput
        label="Repository Prefix"
        model="prefix"
        validators={{ required: true }}
        onBlur={handleChangePrefix}
      />
      <Layout.Space space={26} />
      <SearchBar
        placeholder="Search Repositories"
        value={searchValue}
        setValue={setSearchValue}
      />
      <Layout.Space space={10} />
      <div>
        {filteredRepos
          .slice(
            (paginationProps.state.currentPage - 1) * reposPerPage,
            paginationProps.state.currentPage * reposPerPage
          )
          .map((repo) => {
            return (
              <RepoListItem
                repo={repo}
                org={org}
                prefix={prefix}
                l1={provider ? l1Model : ''}
                trackCheckbox={trackCheckbox}
              />
            );
          })}
      </div>
      <Layout.Space space={6} />
      <Pagination variant="extended" {...paginationProps} />
      <Layout.Space space={16} />
      <Layout.Group>
        <Checkbox
          label="Production Migration"
          model="archive"
          onChange={handleCheckBox}
          aria-haspopup="dialog"
        />
        <Tooltip
          content="Production Migration will archive source repository"
          asChild={false}
          position="right"
        >
          <IconMaterial
            icon="error_outline"
            color="$gray6"
            size="18px"
            css={{
              'abyss-icon-material': {
                marginLeft: '-10px',
              },
            }}
          />
        </Tooltip>
      </Layout.Group>
      <WarningModal
        isOpen={isAckOpen}
        onClose={handleCancel}
        onSubmit={handleAcknowledge}
      />
      <Layout.Space space={26} />
      <Button type="submit">Migrate Repositories</Button>
    </FormProvider>
  );
};
