import { useQuery } from '@abyss/web/hooks/useQuery';
import { config } from '@abyss/web/tools/config';
import GetSecrets from './GetSecrets.graphql';

export const useGetSecrets = (options) => {
  return useQuery(GetSecrets, {
    ...options,
    url: config('GRAPHQL_API_PATH'),
    dataKey: 'secrets',
    initialState: [],
  });
};
