import React, { useEffect, useState, useContext } from 'react';
import { Layout } from '@abyss/web/ui/Layout';
import { Button } from '@src/common/Button';
import { TextInput } from '@abyss/web/ui/TextInput';
import { SelectInput } from '@abyss/web/ui/SelectInput';
import { Checkbox } from '@abyss/web/ui/Checkbox';
import { Alert } from '@abyss/web/ui/Alert';
import { useGetOrganizations } from '@src/hooks/github/useGetOrganizations';
import { useCreateMigrationTask } from '@src/hooks/github/useCreateMigrationTask';
import { useGetResourceGroups } from '@src/hooks/prm/useGetResourceGroups';
import { useGetMetadataFromVitals } from '@src/hooks/github/useGetMetadataFromVitals';
import { useGetSecrets } from '@src/hooks/github/useGetSecrets';
import { useForm } from '@abyss/web/hooks/useForm';
import { useRouter } from '@abyss/web/hooks/useRouter';
import { FormProvider } from '@abyss/web/ui/FormProvider';
import { LoadingOverlay } from '@abyss/web/ui/LoadingOverlay';
import { MaelstromContext } from '@src/abyss/utility/ui/MaelstromProvider/MaelstromContext';
import { Tooltip } from '@abyss/web/ui/Tooltip';
import { IconMaterial } from '@abyss/web/ui/IconMaterial';
import { useGetRepoUserAccess } from '@src/hooks/github/useGetRepoUserAccess';
import { WarningModal, MigrationModal } from '../Modals';

const GIT_REGEX = /^https?:\/\/(github(?:.optum)?|gitlab).com\/[\w-]+\/[\w-]+$/;
const REPO_REGEX = /[^a-zA-Z0-9]/g;

const l1Caps = require('../../../common/ProviderData/l1Capabilities.json');

export const MigrateRepo = () => {
  const form = useForm({
    defaultValues: {
      archive: false,
    },
  });
  const user = useContext(MaelstromContext);
  const router = useRouter();

  const [aideId, setAideId] = useState(null);
  const [provider, setProvider] = useState(false);
  const [secretLink, setSecretLink] = useState(null);
  const [resourceGroup, setResourceGroup] = useState(null);
  const [isAckOpen, setIsAckOpen] = useState(false);
  const [isIssueOpen, setIsIssueOpen] = useState(false);
  const [issueLink, setIssueLink] = useState(null);
  const [hasSeen, setHasSeen] = useState(false);
  const [hasSecrets, setHasSecrets] = useState(false);
  const [organizationOptions, setOrganizationOptions] = useState([]);
  const [resourceGroupOptions, setResourceGroupOptions] = useState([]);
  const [hasPush, setHasPush] = useState(true);
  const routeParams = router.getRouteParams();

  const [getRepoUserAccessResult, getRepoUserAccess] = useGetRepoUserAccess({
    onCompleted: (response) => {
      setHasPush(response.data.isPush);
    },
  });

  const [orgsResult, getOrgs] = useGetOrganizations({
    onCompleted: (response) => {
      const options = response.data.map((dataItem) => {
        return {
          value: dataItem.name,
          label: dataItem.name,
        };
      });

      if (
        routeParams.org &&
        !options.some((e) => {
          return e.value === routeParams.org;
        })
      ) {
        router.navigate('/repository/migrate');
        form.setValue('org', null);
      }

      setOrganizationOptions(options);
    },
  });
  const [migrationTaskResult, createMigrationTask] = useCreateMigrationTask({
    onCompleted: (response) => {
      setIsIssueOpen(true);
      setIssueLink(response.data.data.html_url);
    },
  });
  const [resourceGroupsResult, getResourceGroups] = useGetResourceGroups({
    onCompleted: (response) => {
      const sortedOptions = response.data
        .filter((dataItem) => {
          return dataItem.alias;
        })
        .sort((a, b) => {
          return b.alias - a.alias;
        })
        .map((dataItem) => {
          return {
            value: dataItem.alias,
            label: `${dataItem.alias} (${dataItem.application})`,
          };
        });

      setResourceGroupOptions(sortedOptions);
    },
  });

  const [metadataFromVitalsResult, metadataFromVitals] =
    useGetMetadataFromVitals({
      onCompleted: (response) => {
        setAideId(response.data.aideId);
        form.setValue('aide', response.data.aideId);
        setSecretLink(
          `https://securityplatform.optum.com/appdetails/${response.data.aideId}/Secure_Code_Vulnerabilities`
        );
      },
    });

  const [secretsResult, secrets] = useGetSecrets({
    onCompleted: (response) => {
      if (response.data.secretsCount > 0) {
        setHasSecrets(true);
      } else {
        setHasSecrets(false);
        setSecretLink(null);
      }
    },
  });

  const handleChangeResourceGroup = (value) => {
    const matchedGroup = resourceGroupsResult.data.find((dataItem) => {
      return dataItem.alias === value;
    });

    // if unselecting
    if (!matchedGroup) {
      setResourceGroup(null);
      setAideId(null);
      form.setValue('aide', null);

      const srcUrl = form.getValues('sourceRepoUrl');
      const [repo, org] = srcUrl.split('/').reverse();

      const variables = {
        org,
        repo,
        url: srcUrl,
      };

      metadataFromVitals({ variables });
    }

    if (matchedGroup) {
      setAideId(matchedGroup.askId);
      setResourceGroup(matchedGroup.name);
      form.setValue('aide', matchedGroup.askId);
      form.setValue('resourceGroupName', matchedGroup.name);
    } else if (value === 'rally-health') {
      form.setValue('resourceGroupName', 'rally-health');
    }
  };

  const handleBlurSourceUrl = (e) => {
    const [repo, org] = e.target.value.split('/').reverse();
    if (org === 'AudaxHealthInc') {
      const temp = [...resourceGroupOptions];
      const rally = {
        value: 'rally-health',
        label: 'Rally Health',
      };
      if (
        temp.findIndex((rg) => {
          return rg.value === rally.value;
        }) === -1
      ) {
        temp.push(rally);
      }

      setResourceGroupOptions(temp);
    }
    form.setValue('repositoryName', repo);
    const variables = {
      org,
      repo,
      url: e.target.value,
    };

    setAideId(null);
    form.setValue('aide', null);

    if (!resourceGroup) {
      const results = metadataFromVitals({ variables });
    }

    secrets({ variables });

    getRepoUserAccess({ variables });
  };

  const handleBlurRepoName = (e) => {
    const name = e.target.value;
    const newRepoName = name.toLowerCase().replaceAll(REPO_REGEX, '-');
    form.setValue('repositoryName', newRepoName);
  };

  const handleCheckBox = (e) => {
    if (e.target.checked === true && hasSeen === false) {
      setIsAckOpen(true);
    }
  };

  const checkOrg = (e) => {
    if (e === 'uhc-tech-provider') {
      setProvider(true);
    } else {
      setProvider(false);
    }
  };

  const handleCancel = () => {
    form.setValue('archive', false);
    setIsAckOpen(false);
  };

  const handleAcknowledge = () => {
    setIsAckOpen(false);
    setHasSeen(true);
  };

  const handleClick = () => {
    window.open(issueLink, '_blank', 'noreferrer');
    router.navigate('/');
  };

  const handleClose = () => {
    setIsIssueOpen(false);
  };

  const handleSubmit = (formData) => {
    const variables = {
      repo: provider
        ? `${formData.l1Model}-${formData.repositoryName}`
        : formData.repositoryName,
      org: formData.org,
      aideId: formData.aide,
      url: formData.sourceRepoUrl,
      resourceGroupName: `${formData.resourceGroupName}`,
      resourceGroupAlias: `${formData.resourceGroup}`,
      archive: !!formData.archive,
    };

    createMigrationTask({ variables });
  };

  useEffect(() => {
    getOrgs();
    getResourceGroups({
      variables: {
        msid: user.username,
      },
    });
  }, []);

  useEffect(() => {
    if (routeParams.org) {
      form.setValue('org', routeParams.org);
      checkOrg(routeParams.org);
    }
  }, [routeParams.org]);

  return (
    <LoadingOverlay
      isLoading={migrationTaskResult.isLoading}
      loadingTitle="Loading..."
      loadingMessage="Migrating Repository..."
    >
      <FormProvider state={form} onSubmit={handleSubmit}>
        <TextInput
          label="Source Repository URL"
          model="sourceRepoUrl"
          validators={{
            required: true,
            pattern: {
              value: GIT_REGEX,
              message: 'Invalid Repository URL',
            },
          }}
          onBlur={handleBlurSourceUrl}
          placeholder="https://github.optum.com/org/repo"
        />
        {provider ? (
          <SelectInput
            label="L1 - Capability Model"
            model="l1Model"
            placeholder="Select an L1 - Capability Model"
            validators={{ required: true }}
            isSearchable
            options={l1Caps}
          />
        ) : null}
        <TextInput
          label="Repository Name"
          model="repositoryName"
          validators={{ required: true }}
          onBlur={handleBlurRepoName}
        />
        <Choose>
          <When condition={routeParams.org}>
            <TextInput
              label="Organization"
              validators={{ required: true }}
              model="org"
              isDisabled
            />
          </When>
          <Otherwise>
            <SelectInput
              label="Organization"
              model="org"
              placeholder="Select a target Organization"
              validators={{ required: true }}
              isSearchable
              options={organizationOptions}
              onChange={checkOrg}
              isLoading={orgsResult.isLoading}
            />
          </Otherwise>
        </Choose>
        <SelectInput
          label="Resource Group"
          model="resourceGroup"
          placeholder="Select a Resource Group"
          isSearchable
          isClearable
          options={resourceGroupOptions}
          onChange={handleChangeResourceGroup}
          isLoading={resourceGroupsResult.isLoading}
        />
        <TextInput
          label="AIDE ID"
          model="aide"
          validators={{ required: true }}
          isDisabled={aideId}
        />
        <Layout.Space />
        <Layout.Group>
          <Checkbox
            label="Production Migration"
            model="archive"
            onChange={handleCheckBox}
            aria-haspopup="dialog"
          />
          <Tooltip
            content="Production Migration will archive source repository"
            asChild={false}
            position="right"
          >
            <IconMaterial
              icon="error_outline"
              color="$gray5"
              size="12px"
              css={{
                'abyss-icon-material': {
                  marginLeft: '-15px',
                },
              }}
            />
          </Tooltip>
        </Layout.Group>
        <Layout.Space space={24} />
        <Alert
          title="Error"
          inlineText="You are not a contributor for this repository. You do not have permission for this action."
          isVisible={!hasPush}
        />
        <Layout.Space space={!hasPush ? 24 : 0} />
        <Alert
          title="Error"
          inlineText="This repository has exposed secrets.  Please remediate before migrating"
          actionHref={secretLink}
          actionText="View More"
          isVisible={hasSecrets}
        />
        <Layout.Space space={hasSecrets ? 24 : 0} />
        <Button type="submit" isDisabled={hasSecrets || !hasPush}>
          Migrate Repository
        </Button>
      </FormProvider>
      <WarningModal
        isOpen={isAckOpen}
        onClose={handleCancel}
        onSubmit={handleAcknowledge}
      />
      <MigrationModal
        isOpen={isIssueOpen}
        onClose={handleClose}
        onSubmit={handleClick}
      />
    </LoadingOverlay>
  );
};
