import { useQuery } from '@abyss/web/hooks/useQuery';
import { config } from '@abyss/web/tools/config';
import GetSandboxes from './GetSandboxes.graphql';

export const useGetSandboxes = (options) => {
  return useQuery(GetSandboxes, {
    ...options,
    url: config('GRAPHQL_API_PATH'),
    dataKey: 'sandbox',
    initialState: [],
  });
};
